
import { defineComponent } from 'vue';
import { useTimeout } from '@vueuse/core';

export default defineComponent({
  name: 'AppSpinner',
  props: {
    height: {
      type: String,
      required: true
    },
    width: {
      type: String,
      required: true
    }
  },
  setup() {
    const { ready } = useTimeout(300, true);
    return {
      ready
    };
  }
});
